  margin: 0 auto 8px auto;
}

.format__inner .swiper {
  width: 100%;
}

.format__inner-track {
  display: grid;
  grid-template-columns: repeat(auto-fit, 280px);
  place-content: center;
  gap: 20px;
}

.format__card {
  position: relative;
  width: 280px;
  aspect-ratio: 1.047/1;
  background-color: #fff;
  user-select: none;
}

.format__card-inner {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 225px;
  height: 220px;
  padding: 37px 0 50px 20px;
  border-radius: 10px 10px 0 10px;
  background: url("../img/format-decor1.svg") 0 0/contain no-repeat, linear-gradient(313deg, #ab11db 0%, #c784f9 100%);
}

.format__card-inner::after {
  content: "";
