  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-image: url("../img/format-decor1.svg");
  opacity: 0;
  transition: all 0.3s;
}

.format__card:hover .format__card-inner::after {
  opacity: 1;
}

.format__card-inner-2 {
