  }

  .span-one-transform {
    top: 50%;
    transform: rotate(45deg);
    transition: all 0.3s;
  }

  .span-two-transform {
    transform: rotate(-45deg);
    transition: all 0.3s;
  }

  .span-three-transform {
    display: none;
  }

  .header {
