  }

  .nav {
    position: fixed;
    top: 0;
    right: 0;
    transition: 0.7s;
    width: 100vw;
    height: 100dvh;
    flex-direction: column;
    justify-content: flex-start;
    translate: 100% 0 0;
    padding: 100px 0 0 0;
    background-color: #ffd200;
    z-index: 5;
    overflow: hidden;
  }

  .untranslate {
    translate: 0 0 0;
